.TextInput {
    font-size: large;
    border: 1px solid #EEEEEE;
    padding: 5px;
    border-radius: 3px;
    display: block;
    margin: 5px auto;

  }

  .TextInput:focus {
    outline: none;
    border-color: #719ECE;
    box-shadow: 0 0 10px rgb(50, 100, 153);
  }
  .Button {
    display: block;
    margin: 10px auto;
    padding: .5rem;
    font-size: 1em;
    background: #565B6B;
    border-radius: 5px;
    color: whitesmoke;
    cursor: pointer;
    margin: 5px auto;
  }
